@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background: linear-gradient(
    92.23deg,
    #0ce4c0 21.43%,
    #15b2f0 50.63%,
    #1316c2 100%,
    #001974 117.04%
  );
  box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51);
}

.btn:hover {
  background: linear-gradient(
    92.23deg,
    #001974 21.43%,
    #1316c2 50.63%,
    #15b2f0 100%,
    #0ce4c0 117.04%
  );
}

.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #001974 21.43%,
    #1316c2 50.63%,
    #15b2f0 100%,
    #0ce4c0 117.04%
  );
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-xl font-primary text-white font-medium;
  }
  .btn-sm {
    @apply h-[40px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#1316c2] to-[#15b2f0] font-bold font-primary hover:from-[#15b2f0] hover:to-[#1316c2];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
